i[data-icon-name] {
	line-height: 0;
}

i[data-icon-name] > svg {
	margin: auto;
}

* {
	-webkit-tap-highlight-color: transparent;
	line-height: normal;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	min-height: 0;
	min-width: 0;
	padding: 0;

	border: 0;

	font-size: 100%;
	font: inherit;

	vertical-align: baseline;
	box-sizing: border-box;
}

#root {
	display: flex;
	flex-direction: column;
	height: 100%;
}

ol,
ul {
	list-style: none;
}

/************************************************************/

html {
	height: 100%;
}

body {
	height: 100%;
	overscroll-behavior: none;
	overflow: hidden;
}

div {
	outline: none; /* TODO: why? */
}

button {
	margin: 0;
}
